  <div>
    <div class="left">
      <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
    </div>
    <div class="left chart-wrap order-container">
      <div class="hidden form-list">
        <div class="left sub-title">计划检修列表</div>
        <div class="right">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="状态">
              <el-select v-model="form.value1" placeholder="请选择" size="small">
                <el-option
                  v-for="item in form.orderStatus"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="时间">
              <el-date-picker
                v-model="form.dayDate"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" class="btn-confirm">
              <el-button size="mini" @click.native="searchPlanList">确认</el-button>
            </el-form-item>
            <el-form-item class="btn-confirm-small">
              <el-button icon="el-icon-upload2" size="mini">推送</el-button>
              <el-button icon="el-icon-plus" size="mini">新建</el-button>
              <el-button icon="el-icon-edit-outline" size="mini">修改</el-button>
              <el-button icon="el-icon-close" size="mini">删除</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="hidden">
        <el-table
          height="750"
          :data="planServiceList"
          stripe
          style="width: 100%">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            prop="taskName"
            label="任务名称"
            width="">
          </el-table-column>
          <el-table-column
            prop="customerName"
            label="客户名称"
            width="">
          </el-table-column>
          <el-table-column
            prop="stationName"
            label="电站名称">
          </el-table-column>
          <el-table-column
            prop="serviceSite"
            label="检修地点"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="serviceContent"
            label="检修内容"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="orderStatus"
            label="工单状态">
          </el-table-column>
          <el-table-column
            prop="planStartTime"
            label="计划开始时间">
          </el-table-column>
          <el-table-column
            prop="planEndTime"
            label="计划结束时间">
          </el-table-column>
          <el-table-column
            prop="actualStartTime"
            label="实际开始时间">
          </el-table-column>
          <el-table-column
            prop="actualEndTime"
            label="实际结束时间">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        treeData:[
          {
            id:11,
            label: "斗门区 (12)",
            children: [
              {
                id:21,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:22,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:23,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              }
            ]
          },
          {
            id:12,
            label: "金湾区 (6)",
            children: [
              {
                label: "二级 2-1",
                children: [
                  {
                    label: "三级 2-1-1"
                  }
                ]
              },
              {
                label: "二级 2-2",
                children: [
                  {
                    label: "三级 2-2-1"
                  }
                ]
              }
            ]
          },
          {
            label: "横琴新区 (6)",
            children: [
              {
                label: "二级 3-1",
                children: [
                  {
                    label: "三级 3-1-1"
                  }
                ]
              },
              {
                label: "二级 3-2",
                children: [
                  {
                    label: "三级 3-2-1"
                  }
                ]
              }
            ]
          }
        ],
        form: {
          orderStatus: [],
          value: '',
          value1: '待发布',
          dayDate:[new Date(),new Date()]
        },
        planServiceList: [{
          taskName: 'JX-001—XXXXXX',
          customerName: 'B客户',
          stationName: '小琴站',
          serviceSite:'XX区XX路32号',
          serviceContent:'对XXX设备进行检修',
          orderStatus:'已接收',
          planStartTime:'2018/7/2',
          planEndTime:'2018/7/2',
          actualStartTime:'2018/7/2',
          actualEndTime:'2018/7/2'
        },{
          taskName: 'JX-001—XXXXXX',
          customerName: 'B客户',
          stationName: '小琴站',
          serviceSite:'XX区XX路32号',
          serviceContent:'对XXX设备进行检修',
          orderStatus:'已接收',
          planStartTime:'2018/7/2',
          planEndTime:'2018/7/2',
          actualStartTime:'2018/7/2',
          actualEndTime:'2018/7/2'
        },{
          taskName: 'JX-001—XXXXXX',
          customerName: 'B客户',
          stationName: '小琴站',
          serviceSite:'XX区XX路32号',
          serviceContent:'对XXX设备进行检修',
          orderStatus:'已接收',
          planStartTime:'2018/7/2',
          planEndTime:'2018/7/2',
          actualStartTime:'2018/7/2',
          actualEndTime:'2018/7/2'
        },{
          taskName: 'JX-001—XXXXXX',
          customerName: 'B客户',
          stationName: '小琴站',
          serviceSite:'XX区XX路32号',
          serviceContent:'对XXX设备进行检修',
          orderStatus:'已接收',
          planStartTime:'2018/7/2',
          planEndTime:'2018/7/2',
          actualStartTime:'2018/7/2',
          actualEndTime:'2018/7/2'
        },{
          taskName: 'JX-001—XXXXXX',
          customerName: 'B客户',
          stationName: '小琴站',
          serviceSite:'XX区XX路32号',
          serviceContent:'对XXX设备进行检修',
          orderStatus:'已接收',
          planStartTime:'2018/7/2',
          planEndTime:'2018/7/2',
          actualStartTime:'2018/7/2',
          actualEndTime:'2018/7/2'
        },],
      }
    },
    mounted(){
      let that = this;
      that.getOrderStatus();
      that.getPlanServiceList();
    },
    methods:{
      PVviewById:function(id){

      },
      searchPlanList(){
        let that = this;
        that.getPlanServiceList();
      },
      /*获取工单状态列表*/
      getOrderStatus(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/common/getOrderStatus", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.form.orderStatus = data;
            }
          });
      },
      /*获取计划检修列表*/
      getPlanServiceList(){
        let that = this;
        let params = {
          orderStatus:that.form.orderStatus,
          dayDate:that.form.dayDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getPlanServiceList", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data.data;
              that.planServiceList = data;
            }
          });
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

