<template>
  <div>
    <div class="left">
      <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
    </div>
    <div class="left chart-wrap order-container">
      <div class="hidden">
        <div class="right form-list">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="时间">
              <el-date-picker
                v-model="form.month"
                type="month"
                placeholder="选择月">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" class="btn-confirm-small">
              <el-button size="mini" @click.native="searchAll">确认</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="hidden">
        <el-row :gutter="15" class="top-layout">
          <el-col :span="8"><div class="grid-content bg-purple-dark month-plan-sta">
            <p class="order-title">月度计划统计</p>
            <div id="monthServicePlanSta"></div>
            <el-row :gutter="20" class="margin-top">
              <el-col :span="12"><div class="grid-content bg-purple">
                <p class="quota-item" style="margin-bottom: 0">检修计划 <span class="quota-size blue">{{servicePlan}}</span> 条</p>
              </div></el-col>
              <el-col :span="12"><div class="grid-content bg-purple">
                <p class="quota-item" style="margin-bottom: 0">已归档 <span class="quota-size orange">{{archived}}</span> 条</p>
              </div></el-col>
            </el-row>
          </div></el-col>
          <el-col :span="16"><div class="grid-content bg-purple-dark month-plan-any">
            <p class="order-title">月度计划分析</p>
            <div id="monthPlanAnyChart"></div>
          </div></el-col>
        </el-row>
        <el-row :gutter="15">
          <el-col :span="8"><div class="grid-content bg-purple-dark month-plan-sta">
            <p class="order-title">月度检修统计</p>
            <div id="monthDelStaChart"></div>
          </div></el-col>
          <el-col :span="16"><div class="grid-content bg-purple-dark month-plan-any">
            <p class="order-title">月度故障检修分析</p>
            <div id="monthDelAnyChart"></div>
          </div></el-col>
        </el-row>
      </div>

    </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        treeData:[
          {
            id:11,
            label: "斗门区 (12)",
            children: [
              {
                id:21,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:22,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:23,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              }
            ]
          },
          {
            id:12,
            label: "金湾区 (6)",
            children: [
              {
                label: "二级 2-1",
                children: [
                  {
                    label: "三级 2-1-1"
                  }
                ]
              },
              {
                label: "二级 2-2",
                children: [
                  {
                    label: "三级 2-2-1"
                  }
                ]
              }
            ]
          },
          {
            label: "横琴新区 (6)",
            children: [
              {
                label: "二级 3-1",
                children: [
                  {
                    label: "三级 3-1-1"
                  }
                ]
              },
              {
                label: "二级 3-2",
                children: [
                  {
                    label: "三级 3-2-1"
                  }
                ]
              }
            ]
          }
        ],
        form:{
          month:new Date(),
        },
        servicePlan:0,
        archived:0
      }
    },
    mounted(){
      var that = this;
      that.getServicePlanStaItem();
      that.getServicePlanSta();
      that.getServicePlanAny();
      that.getServicePlanDelSta();
      that.getServiceTroubleAny();
    },
    methods:{
      PVviewById:function(id){

      },
      searchAll(){
        var that = this;
        that.getServicePlanStaItem();
        that.getServicePlanSta();
        that.getServicePlanAny();
        that.getServicePlanDelSta();
        that.getServiceTroubleAny();
      },
      /*获取月度计划统计*/
      getServicePlanSta(){
        let that = this;
        let params = {
          monthDate:that.form.monthDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServicePlanSta", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.getMonthPlanStaChart(data);
            }
          });
      },
      /*获取月度计划分析*/
      getServicePlanAny(){
        let that = this;
        let params = {
          monthDate:that.form.monthDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServicePlanAny", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              var planData = data[0].data;
              var xData = [];
              for (let i = 1; i <= 31; i++) {
                xData.push(i);
              }
              that.getMonthPlanAnyChart(planData,xData);
            }
          });
      },
      /*获取计划统计(检修计划、已归档)*/
      getServicePlanStaItem(){
        let that = this;
        let params = {
          monthDate:that.form.monthDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServicePlanStaItem", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              for(var i=0;i<data.length;i++){
                if(data[i].name == "检修计划"){
                  that.servicePlan = data[i].value;
                }else if(data[i].name == "已归档"){
                  that.archived = data[i].value;
                }
              }
            }
          });
      },
      /*获取月度消缺统计*/
      getServicePlanDelSta(){
        let that = this;
        let params = {
          monthDate:that.form.monthDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServiceSta", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.getMonthDelStaChart(data);
            }
          });
      },
      /*获取月度故障检修分析*/
      getServiceTroubleAny(){
        let that = this;
        let params = {
          monthDate:that.form.monthDate
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServiceTroubleAny", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              var planData = data[0].data;
              var xData = [];
              for (let i = 1; i <= 31; i++) {
                xData.push(i);
              }
              that.getMonthDelAnyChart(planData,xData);
            }
          });
      },
      getMonthPlanStaChart(data) {
        var that = this;
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById("monthServicePlanSta"));
        // 指定图表的配置项和数据
        var option = {
          color:['#37BAF2','#815BF6','#39D5ED','#FFB931','#FD5D71'],
          tooltip: {
            trigger: 'item',
            formatter: "{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            //x: 'right',
            top:40,
            right:50,
            align:'left',
            data:['待发布','已发布','已接收','执行中','完成']
          },
          series: [
            {
              name:'',
              type:'pie',
              center: ['30%', '50%'],
              radius: ['70%', '80%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '24',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:data
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },
      getMonthPlanAnyChart(planData,xData) {
        var myChart = echarts.init(document.getElementById('monthPlanAnyChart'));
        var option = {
          color: ['#209BFD'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            x: "4%",
            y: "15%",
            x2: "1%",
            y2: "8%"
          },
//          legend: {
//            data: ["上月缺陷", "本月缺陷"],
//            align: 'left',
//            right:0
//          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              axisTick: {show: true},
              data: xData,
              axisLabel:{
                color:'#666666'
              },
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '单位：条',
              //min: 0,
              //max: 10,
              nameTextStyle: {
                //Y轴坐标名称颜色
                color: "#666666"
              },
              //Y轴刻度线
              axisTick:{
                show:false
              },
              //Y轴数字
              axisLabel:{
                color:'#666666'
              },
              //横向坐标网格线
              splitLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //X轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              }
            }
          ],
          series: [
            {
              name: '计划执行',
              type: 'line',
              data: planData,
              barWidth:'30%',
            }
          ]
        };
        myChart.setOption(option);
      },
      getMonthDelStaChart(data) {
        var that = this;
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById("monthDelStaChart"));
        // 指定图表的配置项和数据
        var option = {
          color:['#37BAF2','#815BF6','#39D5ED','#FFB931','#FD5D71'],
          tooltip: {
            trigger: 'item',
            formatter: "{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            //x: 'right',
            top:90,
            right:50,
            align:'left',
            data:['计划检修','故障检修']
          },
          series: [
            {
              name:'',
              type:'pie',
              center: ['30%', '50%'],
              radius: ['70%', '80%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '24',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data:data
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },
      getMonthDelAnyChart(planData,xData) {
        var myChart = echarts.init(document.getElementById('monthDelAnyChart'));
        var option = {
          color: [ '#209BFD'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            x: "4%",
            y: "15%",
            x2: "1%",
            y2: "8%"
          },
//          legend: {
//            data: ["上月缺陷", "本月缺陷"],
//            align: 'left',
//            right:0
//          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              axisTick: {show: true},
              data: xData,
              axisLabel:{
                color:'#666666'
              },
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '单位：起',
              //min: 0,
              //max: 10,
              nameTextStyle: {
                //Y轴坐标名称颜色
                color: "#666666"
              },
              //Y轴刻度线
              axisTick:{
                show:false
              },
              //Y轴数字
              axisLabel:{
                color:'#666666'
              },
              //横向坐标网格线
              splitLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              },
              //X轴坐标线
              axisLine:{
                lineStyle:{
                  color:'#e6e8ed'
                }
              }
            }
          ],
          series: [
            {
              name: '故障检修',
              type: 'line',
              data: planData,
              barWidth:'30%',
            }
          ]
        };
        myChart.setOption(option);
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



