<template>
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="检修规则" name="serviceRule"></el-tab-pane>
        <el-tab-pane label="计划检修" name="planService"></el-tab-pane>
        <el-tab-pane label="故障检修" name="troubleService"></el-tab-pane>
        <el-tab-pane label="故障分析" name="troubleAny"></el-tab-pane>
        <el-tab-pane label="月度检修统计" name="monthServiceSta"></el-tab-pane>
      </el-tabs>
      <div>
        <serviceRule v-if="showserviceRule"></serviceRule>
        <planService v-if="showplanService"></planService>
        <troubleService v-if="showtroubleService"></troubleService>
        <troubleAny v-if="showtroubleAny"></troubleAny>
        <monthServiceSta v-if="showmonthServiceSta"></monthServiceSta>
      </div>
    </div>
</template>

<script>
  import serviceRule from "./serviceRule.vue";
  import planService from "./planService.vue";
  import troubleService from "./troubleService.vue";
  import troubleAny from "./troubleAny.vue";
  import monthServiceSta from "./monthServiceSta.vue";
    export default {
        data () {
            return {
              activeName:'serviceRule',
              showserviceRule:true,
              showplanService:false,
              showtroubleService:false,
              showtroubleAny:false,
              showmonthServiceSta:false,
            }
        },
      methods: {
        handleClick(tab, event) {
          var that = this;
          if(tab.paneName == 'serviceRule'){
            that.showserviceRule = true;
            that.showplanService = false;
            that.showtroubleService = false;
            that.showtroubleAny = false;
            that.showmonthServiceSta = false;
          }else if(tab.paneName == 'planService'){
            that.showserviceRule = false;
            that.showplanService = true;
            that.showtroubleService = false;
            that.showtroubleAny = false;
            that.showmonthServiceSta = false;
          }else if(tab.paneName == 'troubleService'){
            that.showserviceRule = false;
            that.showplanService = false;
            that.showtroubleService = true;
            that.showtroubleAny = false;
            that.showmonthServiceSta = false;
          }else if(tab.paneName == 'troubleAny'){
            that.showserviceRule = false;
            that.showplanService = false;
            that.showtroubleService = false;
            that.showtroubleAny = true;
            that.showmonthServiceSta = false;
          }else if(tab.paneName == 'monthServiceSta'){
            that.showserviceRule = false;
            that.showplanService = false;
            that.showtroubleService = false;
            that.showtroubleAny = false;
            that.showmonthServiceSta = true;
          }
        },


      },
      components:{
        serviceRule,planService,troubleService,troubleAny,monthServiceSta
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



