          <div class="right">
            <el-form ref="form" :model="form" label-width="80px">
              <el-form-item label="状态">
                <el-select v-model="form.value1" placeholder="请选择" size="small">
                  <el-option
                    v-for="item in form.orderStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="时间">
                <el-date-picker
                  v-model="form.dayDate"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item label="" class="btn-confirm">
                <el-button size="mini" @click.native="searchPlanList">确认</el-button>
              </el-form-item>
              <el-form-item class="btn-confirm-small">
                <el-button icon="el-icon-upload2" size="mini">推送</el-button>
                <el-button icon="el-icon-plus" size="mini">新建</el-button>
                <el-button icon="el-icon-edit-outline" size="mini">修改</el-button>
                <el-button icon="el-icon-close" size="mini">删除</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="hidden">
          <el-table
            height="750"
            :data="planServiceList"
            stripe
            style="width: 100%">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="taskName"
              label="任务名称"
              width="">
            </el-table-column>
            <el-table-column
              prop="customerName"
              label="客户名称"
              width="">
            </el-table-column>
            <el-table-column
              prop="stationName"
              label="电站名称">
            </el-table-column>
            <el-table-column
