            <el-table-column
              prop="actualStartTime"
              label="实际开始时间">
            </el-table-column>
            <el-table-column
              prop="actualEndTime"
              label="实际结束时间">
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
    export default {
        data () {
            return {
