                  label: "斗门区 (12)",
                  children: [
                    {
                      id:21,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    },
                    {
                      id:22,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    },
                    {
                      id:23,
                      label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
                    }
                  ]
                },
                {
                  id:12,
                  label: "金湾区 (6)",
                  children: [
                    {
                      label: "二级 2-1",
                      children: [
                        {
                          label: "三级 2-1-1"
                        }
                      ]
                    },
                    {
                      label: "二级 2-2",
                      children: [
                        {
                          label: "三级 2-2-1"
                        }
                      ]
                    }
                  ]
                },
                {
                  label: "横琴新区 (6)",
                  children: [
                    {
                      label: "二级 3-1",
                      children: [
                        {
                          label: "三级 3-1-1"
                        }
                      ]
                    },
                    {
                      label: "二级 3-2",
                      children: [
                        {
                          label: "三级 3-2-1"
                        }
                      ]
                    }
                  ]
                }
              ],
              form: {
                orderStatus: [],
                value: '',
                value1: '待发布',
                dayDate:[new Date(),new Date()]
              },
              planServiceList: [{
                taskName: 'JX-001—XXXXXX',
                customerName: 'B客户',
                stationName: '小琴站',
                serviceSite:'XX区XX路32号',
                serviceContent:'对XXX设备进行检修',
                orderStatus:'已接收',
                planStartTime:'2018/7/2',
                planEndTime:'2018/7/2',
                actualStartTime:'2018/7/2',
                actualEndTime:'2018/7/2'
              },{
                taskName: 'JX-001—XXXXXX',
                customerName: 'B客户',
