<template>
  <div>
    <div class="left rule-tree">
      <el-select v-model="typeValue" placeholder="请选择" class="rule-select"@change="changeType">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <div class="devTypeList" v-show="showdevTypeList">
        <div class="left-line"></div>
        <ul class="dev-item-wrap">
          <li v-for="(item, index) in devTypeList" class="type-item" v-bind:class="{active:item.id == currentId}" @click="clickDevType(index)" ><a href="javascript:;">{{item.devName}}</a></li>
        </ul>
      </div>
      <div class="propertyTree" v-show="showpropertyTree">
        <TreeOrg :treeData="treeData" v-on:PVviewById="PVviewById"></TreeOrg>
      </div>
    </div>
    <div class="left chart-wrap order-container">
      <div v-show="showdevTypeList">
        <div class="hidden form-list">
          <div class="left sub-title">设备检修规则设置</div>
          <div class="right btn-confirm-small">
            <el-button icon="el-icon-plus" size="mini">新建</el-button>
            <el-button icon="el-icon-edit-outline" size="mini">修改</el-button>
            <el-button icon="el-icon-close" size="mini">删除</el-button>
          </div>
        </div>
        <div class="hidden">
          <el-table
            height="750"
            :data="devTypeTableData"
            stripe
            style="width: 100%">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="devType"
              label="设备类型"
              width="">
            </el-table-column>
            <el-table-column
              prop="devModel"
              label="设备型号"
              width="">
            </el-table-column>
            <el-table-column
              prop="serviceCycle"
              label="检修周期">
            </el-table-column>
            <el-table-column
              label="操作">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="编辑" placement="bottom">
                  <i class="el-icon-edit icon-option" @click="handleEdit(scope.$index, scope.row)"></i>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="删除" placement="bottom">
                  <i class="el-icon-close icon-option" @click="handleClose(scope.$index, scope.row)"></i>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div v-show="showpropertyTree">
        <div class="hidden form-list">
          <div class="left sub-title">特殊检修设置</div>
          <div class="right btn-confirm-small">
            <el-button icon="el-icon-plus" size="mini">新建</el-button>
            <el-button icon="el-icon-edit-outline" size="mini">修改</el-button>
            <el-button icon="el-icon-close" size="mini">删除</el-button>
          </div>
        </div>
        <div class="hidden">
          <el-table
            height="750"
            :data="propertyTreeTableData"
            stripe
            style="width: 100%">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="customer"
              label="客户"
              width="">
            </el-table-column>
            <el-table-column
              prop="stationName"
              label="电站名称"
              width="">
            </el-table-column>
            <el-table-column
              prop="devName"
              label="设备名称">
            </el-table-column>
            <el-table-column
              prop="tourCycle"
              label="巡视周期">
            </el-table-column>
            <el-table-column
              label="操作">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="dark" content="编辑" placement="bottom">
                  <i class="el-icon-edit icon-option" @click="handleEdit(scope.$index, scope.row)"></i>
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="删除" placement="bottom">
                  <i class="el-icon-close icon-option" @click="handleClose(scope.$index, scope.row)"></i>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TreeOrg from "../../../common/TreeOrg.vue";
  export default {
    data () {
      return {
        options: [{
          value: 'devType',
          label: '设备类型列表'
        }, {
          value: 'property',
          label: '托管资产树'
        }],
        typeValue: 'devType',
        showdevTypeList:true,
        showpropertyTree:false,
        devTypeList:[],
        currentId:1,
        treeData:[
          {
            id:11,
            label: "斗门区 (12)",
            children: [
              {
                id:21,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:22,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              },
              {
                id:23,
                label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)"
              }
            ]
          },
          {
            id:12,
            label: "金湾区 (6)",
            children: [
              {
                label: "二级 2-1",
                children: [
                  {
                    label: "三级 2-1-1"
                  }
                ]
              },
              {
                label: "二级 2-2",
                children: [
                  {
                    label: "三级 2-2-1"
                  }
                ]
              }
            ]
          },
          {
            label: "横琴新区 (6)",
            children: [
              {
                label: "二级 3-1",
                children: [
                  {
                    label: "三级 3-1-1"
                  }
                ]
              },
              {
                label: "二级 3-2",
                children: [
                  {
                    label: "三级 3-2-1"
                  }
                ]
              }
            ]
          }
        ],
        devTypeTableData: [{
          devType: '变压器',
          devModel: 'S13',
          serviceCycle: '3天'
        }],
        propertyTreeTableData: [{
          customer: 'B客户',
          stationName: '小琴电站',
          devName:'1#变压器',
          tourCycle: '3天'
        },{
          customer: 'B客户',
          stationName: '小琴电站',
          devName:'1#变压器',
          tourCycle: '3天'
        },{
          customer: 'B客户',
          stationName: '小琴电站',
          devName:'1#变压器',
          tourCycle: '3天'
        },{
          customer: 'B客户',
          stationName: '小琴电站',
          devName:'1#变压器',
          tourCycle: '3天'
        },{
          customer: 'B客户',
          stationName: '小琴电站',
          devName:'1#变压器',
          tourCycle: '3天'
        },]
      }
    },
    mounted(){
      let that = this;
      that.getDevTypeList();
      that.getServiceRuleSet();
      that.getSpecialRule();
    },
    methods:{
      clickDevType(index){
        var that = this;
        that.currentId = index + 1;
      },
      changeType(){
        var that = this;
        if(that.typeValue == 'devType'){
          that.showdevTypeList = true;
          that.showpropertyTree = false;
        }else if(that.typeValue == 'property'){
          that.showdevTypeList = false;
          that.showpropertyTree = true;
        }
      },
      PVviewById:function(id){

      },
      /*获取设备类型列表*/
      getDevTypeList(){
        let that = this;
        that.$http
          .get(this.BASE_INFO.BASE_URL+"api/common/getDevTypeList", {})
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data;
              that.devTypeList = data;
            }
          });
      },
      /*获取设备巡视规则设置*/
      getServiceRuleSet(){
        let that = this;
        let params = {
          devId:that.currentId
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getServiceRuleSet", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data.data;
              that.devTypeTableData = data;
            }
          });
      },
      /*获取特殊巡视设置*/
      getSpecialRule(){
        let that = this;
        let params = {
          devId:that.currentId
        };
        that.$http
          .post(this.BASE_INFO.BASE_URL+"api/resource/getSpecialRule", params)
          .then(function(response) {
            if(response && response.data && response.data.data){
              var  data=response.data.data.data;
              that.propertyTreeTableData = data;
            }
          });
      },
    },
    components: {
      TreeOrg
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .rule-select.el-select{
    width: 100%;
  }
  .rule-select .el-input__inner{
    border: 0;
    background: #F7F8F9;
  }
</style>



